

import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import Footer from '../layout/footer/footer';
import Modal from 'Components/loading/modalloading'

import style from './ucenter.scss';

class Ucenter extends Component {
    constructor(props) {
        super(props)
    }
    async componentDidMount() {
        let res = await apicloud('user/' + localStorage.userId)
        if (res.success) {
            this.props.dispatch('user/save', { ...res.data })
        }
    }
    render() {
        let { user } = this.props
        let styles = {}
        if (user.get('avatar')) {
            styles.backgroundImage = "url('" + user.get('avatar') + "')"
        }
        return (
            <div className='warper'>
                <div className="header">
                    <div className="h1">个人中心</div>
                </div>
                <div className="content">
                    <div className={style.avatar}>
                        <div className={style.i}>
                            <div className={style.img} style={styles}></div>
                        </div>
                        <div className={style.n}>{user.get('username')}</div>
                    </div>
                    <div className={style.list}>
                        <Link to={'/logout'} className={style.a}>
                            <i className={style.i1 + ' c3 iconfont icon-seeusero'}></i>
                            <div className={style.n}>登出</div>
                            <i className={style.i2 + ' iconfont icon-jiantou-left'}></i>
                        </Link>
                    </div>
                    <Modal />
                </div>
                <Footer />
            </div>
        )
    }
}

export default connect(state => ({
    user: state.get('user'),
}))(Ucenter)


